@import url(https://fonts.googleapis.com/css?family=Pathway+Gothic+One);
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

  background-color: #2A0505;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}


img {
  max-width: 100%;
  max-height: 100%;
}

:root {

  /* beg common */
  --xs-max: 566;
  --sm-max: 767;
  --md-max: 991;
  --lg-max: 1199;
  --xl-min: 1200;

  --font-family-first: sans-serif;
  /* end common */


  /************ xs */

  /* beg font size */
  --font-size-next-base: 18px;
  --font-size-promo: 40px;
  --font-size-footer: 24px;
  --font-size-mod-contact-title: 60px;
  --font-size-music-listen: 10px;
  --font-size-music-btn-action: 18px;
  /* end font size */


  --font-size-base: 20px;
  --font-size-responsive: calc(var(--font-size-base) - 4px);

  --font-size-h1: calc(var(--font-size-responsive) * 2.2);
  --font-size-h2: calc(var(--font-size-responsive) * 1.8);
  --font-size-h3: calc(var(--font-size-responsive) * 1.5);
  --font-size-h4: calc(var(--font-size-responsive) * 1.2);

  /* for elements */
  --el-header-h: 50px;

  --el-slider-promo-h: 45vw;
  --el-slider-promo-min-h: 200px;
  --el-slider-promo-max-h: 50vh;

  --el-bg-promo-h: 35vw;
  --el-bg-promo-min-h: 200px;
  --el-bg-promo-max-h: 40vh;


  /* prev old @deprecated */
  --font-color-primary: #485F6E;
  --font-color-secondary: #39c360;

  --font-color-link-ext: #7396ad;


  --indent-el: 10px;
  --indent-block: 20px;
  --indent-section: 40px;
  --indent-xxxl: 80px;

  --border-description: 1px solid #979797;

  --color-border-num: #979797;

  --color-border-light: #D8D8D8;


  --bg-body: transparent;
}



/* sm */
@media (min-width: 576px) {
  :root {
    --font-size-next-base: 5vw;
    --font-size-promo: 7vw;
    --font-size-music-listen: 14px;
    --font-size-music-btn-action: 24px;
  }
}



/* md */
@media (min-width: 768px) {
  :root {
    --font-size-music-listen: 16px;
    --font-size-music-btn-action: 30px;

    --font-size-responsive: 2vw;
    --el-header-h: 86px;
  }
}



/* lg */
@media (min-width: 992px) {
  :root {
    --font-size-responsive: 1.5vw;
    --font-size-music-listen: 12px;
  }
}



/* xl */
@media (min-width: 1200px) {
  :root {
    --font-size-next-base: 60px;
    --font-size-promo: 84px;
    --font-size-responsive: 1.3vw;

    --font-size-music-listen: 16px;
  }
}





